import React from "react";
import "./DisplayValues.css";
import {Bar} from 'react-chartjs-2';



const Display = ({array,status}) =>{

var graphStatus = status
var p = Math.random();
var colorValue;

let array1 = []

	array.map((data)=>{
		console.log(data["yearSort"])
		return array1.push(data)
	})

let array2 = array1.sort(function(a,b){
	
	return (a["monthSort"]-b["monthSort"])})

let array3 = array2.sort(function(a,b){
	
	return (a["yearSort"]-b["yearSort"])})

let arrayAmount = array3.map((data)=>{
	return data['amountSort']
})

let arraymonth = array3.map((data)=>{
	return data['month']
})

let arrayChange = [0];
let arrayPercent = [0];

if(arrayAmount.length > 1){
	for(let i=1;i<arrayAmount.length;i++){
		let changeofamount = arrayAmount[i]-arrayAmount[i-1];
		let changeofpercent = (changeofamount/arrayAmount[i-1])*100
		arrayChange.push(changeofamount);
		arrayPercent.push(changeofpercent.toFixed(2))
	}
}


let arrayMain =[];

for (let j=0;j<arrayAmount.length;j++){
	arrayMain.push({
		amount : arrayAmount[j],
		month:arraymonth[j],
		change:arrayChange[j],
		percent:arrayPercent[j],
		percentchange:arrayPercent[j]+" %"
	})
}

	
const displayValue = arrayMain.map((data)=>{
	

	
	(Number(data["change"])>0)? colorValue="colorGreen": 
	(Number(data["change"])<0)? colorValue = "colorRed" : colorValue = "none"

		return(
				<div id="headertitle">
					<div id="title1" key = {p}>
						<span className="valueSpan1"> <h3 className="title1" > {data["month"]}</h3> </span>
						<span className="valueSpan2"> <h3 className="title1"> {data["amount"]}</h3></span>
						<span id={colorValue} className="valueSpan3"   > <h3 className="title1"   > {data["change"]}</h3> </span>
						<span  id={colorValue} className="valueSpan4" >	<h3 className="title1" > {data["percentchange"]} </h3>  </span>
					</div>
				</div>	
			)
		

		 
})


return(

		<div id = "area">
			{displayValue}
		{
			(status)? <div id="graph">	
		       <Bar

		        data = 
		          {{labels: arraymonth,

		            datasets:[

		              {
		                label:"Networth(â‚¹)",
		                data: arrayAmount,
		                backgroundColor: 'rgba(0,0,255,0.5)',
		                yAxisID : "A",

		                

		              },
		              {
		                type:"line",
		                label:"Change(%)",
		                data: arrayPercent,
		                borderColor:"red",
		                backgroundColor:"rgba(0,0,0,0)",
		                yAxisID : "B",

		              }

		            ]

		          }}
		        

		        height = {300}

		        width = {20}

		        options = {{
		          maintainAspectRatio : false,
		          scales: {
		                  yAxes: [{
		              id: 'A',
		              type: 'linear',
		              position: 'left',
		              ticks: {
		                beginAtZero:true
		              }
		            }, {
		              id: 'B',
		              type: 'linear',
		              position: 'right',
		              ticks: {
		                
		              }
		            }]
		        }
		        }}

	       />
	      </div> : <span> </span>
		}	
       </div>

	)







}




export default Display;